.arLoginBg{
    background: url(../../../assets/images/login-bg.svg);
    background-size: cover;
}

.rightLoginBg{
    background: #FFFFFF;
    @media screen and (max-width: 480px){
        display: none;
    }
}

.login-sec .card{
    border: none;
    border-radius: 50px;
}

.login-sec .card-group{
    filter: drop-shadow(0px 10px 20px rgba(0, 0, 0, 0.15));
}

.login-sec .card-body{
    text-align: center;
}



.login-sec .input-group label{
    margin-right: 20px;
}

.login-sec form label{
    display: flex;
    justify-content: left;
    align-items: center;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
    color: #000000;

    @media screen and (max-width: 480px){
        font-size: 12px;
    }
}

.login-sec {
    .each_form_group{
        margin-bottom: 20px;
    }
    #keepMeLogin{
        margin-top: 0;
    }
    .form-control{
        height: 50px;
        padding: 10px 22px;
    }
    .icons{
        margin-left: 0;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        @media screen and (max-width: 767px) {
            font-size: 14px;
        }
    }
   
    .icons{
        .para_1{
            font-size: 14px;
            color: #000000;    
            margin-left: 10px;       
        }
    } 
    .para_2{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 15px;
        color: #CC66CC;
        display: flex;
        justify-content: flex-end;
        align-items: center;
       
    }
    .btn.btn-primary{
        background: linear-gradient(90deg, #0D9BF1 0%, #8D47FF 99.98%, #6A68FA 99.99%, #9B4DFF 100%);
        border-radius: 10px;
        border: none;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        text-transform: uppercase;
        color: #FFFFFF;
        filter: drop-shadow(0px 10px 20px rgba(0, 0, 0, 0.15));
        padding: 13px 51px;
        transition: all ease-in-out 300ms;
        &:hover{
            background: linear-gradient(270deg, #0D9BF1 0%, #8D47FF 99.98%, #6A68FA 99.99%, #9B4DFF 100%);
        }
        @media screen and (max-width: 480px){
            margin-left: 98px;
            font-size: 12px;
        }
        @media screen and (max-width: 768px){
            margin-left: 89px;          
            font-size: 10px;
        }
    }
    .para_3{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        text-transform: uppercase;
        color: #FFFFFF;
    }   
    .card-body {
    .input-group {
    input{
        background: #FFFFFF;
        border: 1px solid #D2D2D2;
        border-radius: 50px;
            }
        }
    }
} 
